<template>
  <div class="page page-user">
    <van-cell-group :border="false">
      <van-cell class="userInfo">
        <template #title>
          <div class="avatarBox">
            <van-uploader
              :after-read="afterRead"
              :preview-image="false"
              max-count="1"
              result-type="file"
              @click.native="(e) => onClickUpload(e)"
            >
              <van-image
                :src="$root.getImageUrl(userInfo.avatar)"
                fit="cover"
                round
              >
                <div slot="error" class="defaultAvatar size60"></div>
              </van-image>
            </van-uploader>
            <div class="icon-camera"></div>
          </div>
        </template>
        <div class="info">
          <div class="username" @click="$router.push('/user_nickname')">
            {{ userInfo.nickname }}
            <van-icon name="edit"/>
          </div>
          <div class="walletAddr">
            <span class="van-ellipsis">
              钱包地址 {{ userInfo.walletAddress }}
            </span>
            <button
              v-if="userInfo.walletAddress"
              v-clipboard="userInfo.walletAddress"
              class="copy copyBtn"
              @success="$root.copySuccessful"
            ></button>
          </div>
          <div class="credit" @click="$router.push('/user_credit')">
            信用等级
            <div class="volume">
              <div
                v-for="v in 5"
                :key="v"
                :class="{
                  [`g${largeLevel}${
                    (
                      v == 5
                        ? levelModel == 0
                        : levelModel >= v || levelModel == 0
                    )
                      ? 'l'
                      : ''
                  }`]: true,
                }"
                class="grade mini"
              />
            </div>
          </div>
        </div>
        <template #right-icon>
          <div
            :class="{ pass: userInfo.isRealNameAuth }"
            class="creditStatus"
          >
            <div class="icon-certificate"></div>

            {{ userInfo.isRealNameAuth ? "已认证" : "未认证" }}
          </div>
        </template>
      </van-cell>
      <van-cell is-link title="个人账变" to="user_bill"></van-cell>
      <van-cell is-link title="个人转账" to="user_transfer"></van-cell>
    </van-cell-group>
    <div class="gray-8"></div>
    <van-cell-group>
      <van-cell
        :is-link="userInfo.auditRealNameStatus === ''"
        :to="userInfo.isRealNameAuth ? '' : '/user_certify'"
        title="实名认证"
      >
        <template>
          <span v-if="userInfo.isRealNameAuth" style="color: #07ca5e">
            已实名认证
          </span>
          <span
            v-else-if="userInfo.auditRealNameStatus === 0"
            style="color: #6f5bfe"
          >
            审核中
          </span>
          <span
            v-else-if="userInfo.auditRealNameStatus === 2"
            style="color: #fb2222"
          >
            审核不通过
          </span>
        </template>
      </van-cell>
      <van-cell to="/user_security" is-link title="安全中心"></van-cell>
      <van-cell :to="userInfo.userName ? '' : '/user_name'"
                :is-link="!userInfo.userName" title="用户名">
        {{ userInfo.userName || "未设置用户名" }}
      </van-cell>
      <van-cell is-link title="登录设备管理" to="/user_devices"></van-cell>
      <van-cell is-link title="意见反馈" to="/user_report"></van-cell>
      <van-cell
        is-link
        title="异常交易行为以及平台处理措施"
        to="/panic"
      ></van-cell>
      <van-cell is-link title="关于我们" to="/user_us">
        版本V{{ $root.version }}
      </van-cell>
    </van-cell-group>

    <van-button class="primary block" @click="exit"> 退出登录</van-button>
    <div v-if="!isErude" id="debug-block" class="debug" @click="onDebug"></div>
  </div>
</template>

<script>
import {upload} from "@/apis/common";
import {setAvatar} from "@/apis/user";
import {setAuthentication} from "@/vendor/q";
import { blobToDataURI } from '@/vendor/file'

export default {
  data() {
    const erude = document.getElementById("eruda-script");
    return {
      isErude: !!erude,
      count: 0,
      lastPressTimestamp: 0,
    };
  },
  created() {
    this.$root.$emit("root:set-topbar", ["left-back", "customer-service"]);
    this.$root.fetchUserInfo();
  },
  computed: {
    levelModel() {
      return this.userInfo.creditLevel % 5;
    },
    largeLevel() {
      if (this.levelModel >= 1) {
        return (this.userInfo.creditLevel - this.levelModel) / 5 + 1;
      } else {
        return this.userInfo.creditLevel / 5;
      }
    },
    userInfo() {
      return this.$root.userInfo || {}
    }
  },
  methods: {
    onClickUpload(e) {
      if (this.$root.isH5Plus) {
        e.preventDefault();
        window.h5p.uploadImage(async (blob, name, type, path) => {
          console.log(blob, name, type, path);
          if (blob) {
            var fs = new File([blob], name, {
                type: type,
            });
            this.afterRead({file: fs, _filePath: path});
          } else {
            this.$toast.fail("获取图片失败");
          }
        });
      }
    },
    afterRead(file) {
      upload({
        _file: file.file,
        imgFile: this.$root.isH5Plus ? file._filePath : file.file, 
        type: "avatar"
      }).then(
        ({ok, data, msg}) => {
          if (ok) {
            setAvatar({avatarAddr: data.name}).then(({ok, msg}) => {
              if (ok) {
                this.userInfo.avatar = data.name;
              } else {
                this.$toast.fail(msg);
              }
            });
          } else {
            this.$toast.fail(msg);
          }
        }
      );
    },
    exit() {
      this.$root.exitLogin()
    },
    onDebug() {
      const now = Date.now();
      console.log(now - this.lastPressTimestamp);
      if (now - this.lastPressTimestamp > 500 && this.lastPressTimestamp > 0) {
        this.lastPressTimestamp = 0;
        this.count = 0;
      } else {
        this.lastPressTimestamp = now;
        this.count += 1;
      }
      if (this.count > 6) {
        localStorage.setItem("debug", 1);
      }
    },
  },
};
</script>

